import React, { Component } from 'react';
import MainGuidePage from '../../MainGuidePage';
import SelectMenu from '../../../public/SelectMenu';
import ActiveItem from './ActiveItemCards/ActiveItem';
import { Button } from 'antd';
import './index.css';

const options = [
  {
    label: '',
    value: 'docs',
  },
  {
    label: '',
    value: 'knowledge',
  },
  {
    label: '',
    value: 'team',
  }
]

export default class Main extends Component {
  state = {
    isNew: true,
    newTemplateList: [],
    hasNoData: false,
    activeList: [],
  }

  async componentDidMount() {
    const { newTemplateList, activeList } = this.state;
    const titleArr = ['', '', '', '', '', ''];
    const descArr = ['', '', '', '', '', '']
    for (let i = 0; i< 6; i++) {
      newTemplateList.push({
        id: `${i}8000${i + 1}${i + 2}`,
        title: titleArr[i],
        desc: descArr[i],
      });
      activeList.push({
        id: `${i}8000${i + 1}${i + 2}`,
        title: '',
        courseName: '',
        groupName: '',
        knowledgeName: '',
        updateTime: '2020-2-22 20:42',
        changeAuthor: '',
        behavior: 'update',
        content: ''
      });
    }

    this.setState({
      newTemplateList,
      activeList,
    })
  }

  SelectItems = values => {
    if (values.length === 0) {
      this.setState({
        hasNoData: true,
      })
    } else {
      this.setState({
        hasNoData: false,
      })
      console.log('');
    }
  }

  onEdit = item => {
    console.log(item, '')
  }

  getMoreData = () => {
    const { activeList } = this.state;
    for (let i = 0; i < 6; i++) {
      activeList.push({
        id: `${i}8000${i + 1}${i + 4}`,
        title: '',
        courseName: '',
        groupName: '',
        knowledgeName: '',
        updateTime: '2020-2-22 20:42',
        changeAuthor: '',
        behavior: 'update',
        content: ''
      });
    };
    this.setState({
      activeList,
    })
  }

  render() {
    const { isNew, newTemplateList, hasNoData, activeList } = this.state;

    return <>
      {
        isNew && newTemplateList.length > 0 && <MainGuidePage list={newTemplateList} />
      }
      <div className="dashboard-main-page">
        <div className="latest-new">
          <SelectMenu
            options={options} 
            onChange={(values) => this.SelectItems(values)}
          />
          {
            hasNoData && (
              <div></div>
            )
          }
          {
            !hasNoData && <>
              {activeList.map(item => <ActiveItem key={item.id} onEdit={() => this.onEdit(item)} activeItem={item}></ActiveItem>)}
              <Button 
                onClick={() => this.getMoreData()}
                style={
                  {
                    width: '100%', 
                    lineHeight: '40px', 
                    height: '60px',
                    border: 0,
                    boxShadow: '0px 3px 14px rgba(0,0,0,0.04)'
                  }
                }
              ></Button>
            </>
          }
        </div>
        <div className="right-area">
          <div className="today-support">
            
            <div className="support-image">
              <div className="actions-bar">
                <span></span>
                <span></span>
              </div>
            </div>
          </div>
          <div className="quickly-entry">
            
            <ul className="entries">
              <li>--</li>
              <li><a></a></li>
            </ul>
          </div>
        </div>
      </div>
    </>
  }
}