import React from 'react';
import { Form, Input, Button } from 'antd';
import { connect } from 'mirrorx';
import { updatePSW } from '../../../api/user';
import '../index.css';

const ChangePassword = (props) => {
  const { userCode, password } = props;
  console.log(props)

  const onFinish = params => {
    console.log(params, 'params')
    const { 
      userCode,
      oldPassword,
      newPassword,
    } = this.state;
    updatePSW({
      userCode,
      oldPassword,
      newPassword,
    }).then(res => {
      console.log(res, 'res', '')
    })
  }

  return (
    <Form
      className="login-form"
      initialValues={{
        userCode,
        oldPassword: password,
        newPassword: '',
        confirm: '',
      }}
      onFinish={(values) => onFinish(values)}
    >
      <h2 className="changeText">
        
      </h2>
      <p className="desc-paragraph">
        
      </p>
      <Form.Item
        name="userCode"
        rules={[{ required: true, message: '/' }]}
        style={{margin: 0,}}
      >
        <Input 
          addonBefore=""
          placeholder=""
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="oldPassword"
        rules={[{ required: true, message: '' }]}
        style={{margin: '12px 0 0 0'}}
        >
        <Input.Password
          placeholder=""
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="newPassword"
        rules={[{ required: true, message: '' }]}
        style={{margin: '12px 0'}}
        >
        <Input.Password
          placeholder=""
          size="large"
        />
      </Form.Item>
      <Form.Item
        name="confirm"
        rules={[
          { required: true, message: '' }, 
          ({ getFieldValue }) => ({
            validator(rule, value) {
              if (!value || getFieldValue('newPassword') === value) {
                return Promise.resolve();
              }
              return Promise.reject('');
            },
          }),
        ]}
        
        >
        <Input.Password
          placeholder=""
          size="large"
        />
      </Form.Item>
      <Button type="primary" size="large" htmlType="submit"></Button>
    </Form>
  )
}

export default connect(state => state.userInfo)(ChangePassword)