import React, { Component } from 'react';
import SearchBar from '../../../../public/SearchBar';
import moment from 'moment';
import SharedTable from '../../../../public/SharedTable';
import DialogModal from '../../../../public/DialogModal/DialogModal';
import { message } from 'antd';

export default class KnowledgeRecycle extends Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: '',
        key: 'docName',
        width: 210,
      }, 
      { 
        title: '',
        key: 'behavior',
        width: 210,
      },
      // {
      //   title: '',
      //   key: 'belong',
      //   width: 240,
      // },
      {
        title: '',
        key: 'deleteTime',
        width: 100,
      },
      {
        title: '',
        width: 150,
        render: (item, index) => {
          return <div className="action-btns">
            <a href="javascript:;" onClick={() => this.reOpen(item, index)}></a>
            <a href="javascript:;" onClick={() => this.delete(item, index)}></a>
          </div>
        }
      }
    ];

    this.state = {
      dataList: [],
      keywords: undefined,
      columns,
      checkedIds: [],
      loading: false,
    }
  }

  componentDidMount = () => {
    this.fetchData();
  }

  fetchData = () => {
    this.setState({
      loading: true,
    });

    setTimeout(() => {
      const { keywords } = this.state;
      const arr = [];
      const date = moment().format('LT');
      // mock
      for (let i = 0; i < 10; i++) {
        arr.push({
          docName: keywords || '1',
          id: `2huhjkd${i}`,
          deleteTime: date,
          behavior: '',
        });
      };
  
      this.setState({
        dataList: arr,
        loading: false,
      });

    }, 600)
  }

  reOpen = (item, index) => {
    DialogModal.show({
      title: '',
      content: '',
      onOk: async () => {
        console.log('');
        await this.fetchData();
        message.success('');
      },
      type: 'warning',
    });
    console.log('', item)
  }

  delete = (item, index) => {
    DialogModal.show({
      title: '',
      content: '',
      onOk: async() => {
        console.log('');
        await this.fetchData();
        message.success('');
      },
      type: 'warning'
    });
    console.log('', item)
  }

  onSearchDocs = keywords => {
    this.setState({
      keywords,
    }, () => this.fetchData());
  }

  renderExtraBtn = () => {
    return (
      <div className="batchBtn">
        <span onClick={() => this.batchReOpen()} className="link"></span>
        <span onClick={() => this.batchDelete()} className="link"></span>
      </div>
    )
  }

  batchReOpen = () => {
    const { checkedIds } = this.state;
    DialogModal.show({
      title: '',
      content: `${checkedIds.length}`,
      onOk: async() => {
        console.log('');
        await this.fetchData();
        message.success('');
      },
      type: 'warning'
    });
    console.log('', checkedIds);
  }

  batchDelete = () => {
    const { checkedIds } = this.state;
    DialogModal.show({
      title: '',
      content: `${checkedIds.length}`,
      onOk: async() => {
        console.log('');
        await this.fetchData();
        message.success('');
      },
      type: 'warning'
    });
    console.log('', checkedIds);
  }

  getCheckedList = values => {
    console.log(values, 'values')
    this.setState({
      checkedIds: values,
    })
  }

  render() {
    const { columns, dataList, loading } = this.state;
    return (
      <div className="knowledgeRecycle">
        <SearchBar 
          onSearch={values => this.onSearchDocs(values)} 
          searchTitle=""  
        />
        <SharedTable 
          loading={loading}
          renderExtraBtn={() => this.renderExtraBtn()} 
          hasCheckedBox 
          columns={columns} 
          dataSource={dataList} 
          getCheckedList={values => this.getCheckedList(values)}
        />
      </div>
    )
  }
}