import React, { Component } from 'react';
import SharedTable from '../../../../public/SharedTable';
import SearchBar from '../../../../public/SearchBar';
import { Dropdown, Menu } from 'antd'; 
import { CaretDownOutlined, CaretUpOutlined } from '@ant-design/icons';

import './index.css';

export default class GroupMember extends Component {
  constructor(props) {
    super(props);

    const columns = [
      {
        title: '',
        key: 'memberName',
        width: 240,
      },
      {
        title: '',
        key: 'id',
        width: 320,
        render: item => <p className="paragraph">{item.id}</p>
      },
      {
        title: '',
        key: 'memberRole',
        width: 320,
        render: (item, index) => {
          if (index === 0) {
            return <div className="desc-paragraph"></div>
          }
          return <Dropdown 
            overlay={this.renderMenu(index)}
          >
            <span className="role">
              <p>{item.memberRole}</p>
              <CaretDownOutlined style={{color: '#999'}} />
            </span>
          </Dropdown>
        }
      },
      {
        title: '',
        key: 'order',
        width: 80,
        render: (item, index) => {
          if (item.order === 0) {
            return <a onClick={() => this.quit(item)}></a>
          } 
          return <a onClick={() => this.delete(item)}></a>
        }
      }
    ]

    this.state = {
      columns,
      dataSource: [],
    }
  }

  
  renderMenu = index => {
    return <Menu 
      onClick={e => this.selectRoot(e, index)}
    >
      <Menu.Item
        key=""
        >
        <h3></h3>
        <p></p>
      </Menu.Item>
      <Menu.Item
        key=""
        >
        <h3></h3>
        <p></p>
      </Menu.Item>
      <Menu.Item
        key=""
        >
        <h3></h3>
        <p></p>
      </Menu.Item>
      <Menu.Divider />
      <Menu.Item
        key="setting"
      >
        <a style={{color: '#3A92F9'}} className="settingBtn" href=""></a>
      </Menu.Item>
    </Menu>
  }
  
  selectRoot = (values, index) => {
    const { key } = values;
    const { dataSource } = this.state;
    dataSource[index].memberRole = key;
    this.setState({
      dataSource,
    })
  }

  quit = item => {
    console.log(item, '');
  }

  delete = item => {
    console.log(item, '')
  }

  componentDidMount() {
    const { dataSource } = this.state;
    const roles = ['', '', '']

    for (let i = 0; i < 6; i++) {
      const order = parseInt(Math.random() * 3);
      dataSource.push({
        memberName: '',
        id: `31161704010${i}`,
        memberRole: roles[order],
        order: i === 0 ? 0 : 1,
      })
    }

    this.setState({
      dataSource,
    })
  }

  render() {
    const { columns, dataSource } = this.state;
    return <div className="knowledge-list">
      <SearchBar searchTitle="" placeholderText="" searchBtnText="" />
      <SharedTable hasCheckedBox dataSource={dataSource} hasCheckedBox={true} columns={columns} /> 
    </div>
  }
}