import React, { Component } from 'react';
import SharedTable from '../../../public/SharedTable';
import SearchBar from '../../../public/SearchBar';

import './index.css';

export default class Course extends Component {
  constructor(props) {
    super(props);
    const columns = [
      {
        title: '',
        key: 'courseName',
        width: 320,
      },
      {
        title: '',
        key: 'courseAttr',
        width: 240,
      },
      {
        title: '',
        key: 'courseDesc',
        width: 360,
      },
      {
        title: '',
        key: 'courseTeacher',
        width: 160
      }
    ];

    this.state = {
      columns,
      courseList: [],
    }
  }

  componentDidMount() {
    const { courseList } = this.state;
    const titleArr = ['', '', '', '', '', ''];
    const attrArr = ['', '', '', ''];
    for (let i = 0; i < 6; i++) {
      const number = parseInt(Math.random() * 4);
      courseList.push({
        id: `2y7uy3u4${i}`,
        icon: () => <img src={require('../../../assets/images/logo.png')} />,
        courseName: titleArr[i],
        courseAttr: attrArr[number],
        courseDesc: '',
        courseTeacher: ['', '']
      })
    };

    this.setState({
      courseList,
    })
  }

  onSearch = searchParams => {
    console.log(searchParams, '')
  }

  render() {
    const { columns, courseList } = this.state;
    return <div className="course-list">
      <SearchBar 
        onClick={() => {console.log('222')}} 
        onSearch={values => this.onSearch(values)} 
        searchTitle="" 
        searchBtnText="" />
      <SharedTable parentPath="#/course/detail/" columns={columns} dataSource={courseList} />
    </div>
  }
}