import React, { Component } from 'react';
import { Menu, Input, Button, Avatar, Checkbox, message } from 'antd';

export default class GroupSetting extends Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedKeys: ['messageInfo'],
      userInfos: {
        name: '',
        major: '',
        code: '311617040124',
        nickName: 'ZJUE',
        desc: '',
        phone: '180741310353',
        knowledgeCount: 100,
        teamCount: 36,
        courseCount: 32,
      },
      messageList: [
        {
          type: '',
          desc: '',
          id: 'knowledgeChange',
          isOpen: true,
        }, 
        {
          type: '',
          desc: '',
          id: 'knowledgeContentChange',
          isOpen: false,
        },
        {
          type: '',
          desc: '',
          id: 'commentUpdate',
          isOpen: false,
        },
        {
          type: '',
          desc: '',
          id: 'teamChange',
          isOpen: false,
        },
        {
          type: '',
          desc: '',
          id: 'newComment',
          isOpen: false,
        },
        {
          type: '',
          desc: '',
          id: 'aboutMe',
          isOpen: false,
        },
        {
          type: '',
          desc: '',
          id: 'beConcerned',
          isOpen: false,
        },
        {
          type: '',
          desc: '',
          id: 'isFavoriteMe',
          isOpen: false,
        },
        {
          type: '',
          desc: '',
          id: 'teamInvite',
          isOpen: true,
        },
        {
          type: '',
          desc: '',
          id: 'applyForTeam',
          isOpen: true,
        },
        {
          type: '',
          desc: '',
          id: 'joinCourse',
          isOpen: true,
        },
        {
          type: '',
          desc: '',
          id: 'pushArticles',
          isOpen: true,
        },
      ]
    }
  }

  onSelect = e => {
    this.setState({
      selectedKeys: [e.key]
    })
  }

  updateUserInfos = () => {
    const { userInfos } = this.state;

    if (!userInfos.nickName) {
      message.warn('');
      this.setState({
        userInfos: {
          ...userInfos,
          nickName: userInfos.name,
        }
      })
    };

    console.log('');
  }

  renderBasicInfo = () => {
    const { userInfos } = this.state;
    return (
      <div className="page-section">
        <h3>
          
        </h3>
        <div className="main-section">
          <section className="section-person flex">
            <div className="label"></div>
            <div className="desc-paragraph">
              {userInfos.name}
            </div>
          </section>
          <section className="section-person flex">
            <div className="label"></div>
            <div className="desc-paragraph">
              {userInfos.major}
            </div>
          </section>
          <section className="section-person flex">
            <div className="label"></div>
            <div className="desc-paragraph">
              {userInfos.code}
            </div>
          </section>
          <section className="part">
            <div className="label"></div>
            <Input 
              value={userInfos.nickName} 
              style={{width: 420, borderRadius: '4px'}} 
              onChange={(e) => {
                const { userInfos } = this.state;

                this.setState({
                  userInfos: {
                    ...userInfos,
                    nickName: e.target.value,
                  }
                })
              }}
            />
          </section>
          <section className="part">
            <div className="label"></div>
            <Input.TextArea 
              value={userInfos.desc} 
              style={{width: 420, borderRadius: '4px'}} 
              onChange={(e) => {
                const { userInfos } = this.state;

                this.setState({
                  userInfos: {
                    ...userInfos,
                    desc: e.target.value,
                  }
                })
              }}
            />
          </section>
          <section className="part">
            <div className="label"></div>
            <div className="avatar">
              <div className="left-area">
                <Avatar size={64} />
                <div className="addBtn">
                  <Button style={{color: '#3A92F9', borderColor: '#3A92F9'}}></Button>
                  <p className="desc-paragraph">
                    
                  </p>
                </div>
              </div>
              <Button onClick={() => this.updateUserInfos()} className="updateBtn"></Button>
            </div>
          </section>
        </div>
      </div>
    )
  }

  renderAccountManager = () => {
    const { userInfos } = this.state;
    return (
      <>
        <div className="page-section accountManager">
          <h3>
            
          </h3>
          <div className="main-section">
            <div className="card-item flex border">
              <dl>
                <dt></dt>
                <dd className="desc-paragraph">{userInfos.phone.replace(userInfos.phone.slice(3, 8), '****')}</dd>
              </dl>
              <span className="curser-pointer"></span>
            </div>
            <div className="card-item flex border">
              <dl>
                <dt></dt>
                <dd className="desc-paragraph">
                  
                </dd>
              </dl>
              <span className="curser-pointer"></span>
            </div>
          </div>
        </div>
        <div className="page-section">
          <h3>
            
          </h3>
          <div className="main-section account-section">
            <p className="desc-paragraph">
               {userInfos.knowledgeCount}  {userInfos.teamCount}  {userInfos.courseCount} 
            </p>
            <p className="desc-paragraph">
              
            </p>
            <Button disabled={true} type="danger"></Button>
          </div>
        </div>
      </>
    )
  }

  onChangeItem = (item, index) => {
    const { messageList } = this.state;

    messageList[index].isOpen = !item.isOpen;

    this.setState({
      messageList,
    })
  }

  renderMessageInfo = () => {
    const { messageList } = this.state;
    return (
      <>
        <div className="page-section">
          <h3>
            
          </h3>
          <div className="main-section">
            {
              messageList.map((item, index) => {
                return <div className="card-item flex" style={{marginBottom: '21px', borderBottom: '1px solid #e5e5e5'}}>
                  <dl>
                    <dt>
                      {item.type}
                    </dt>
                    <dd className="desc-paragraph">
                      {item.desc}
                    </dd>
                  </dl>
                  {
                    item.isOpen ? 
                      <Button onClick={() => this.onChangeItem(item, index)}></Button> :
                      <Button onClick={() => this.onChangeItem(item, index)} className="defaultBtn"></Button>
                  }
                </div>
              })
            }
          </div>
        </div>
      </>
    )
  }

  render() {
    const { selectedKeys } = this.state;

    return (
      <div className="group-setting" style={{marginTop: '32px'}}>
        <div className="side-tab">
          <Menu onSelect={this.onSelect} selectedKeys={selectedKeys} className="menu" mode="inline">
            <Menu.Item key="basicInfo" className="menu-item">
              
            </Menu.Item>
            <Menu.Item key="accountManager" className="menu-item">
              
            </Menu.Item>
            <Menu.Item key="messageInfo" className="menu-item">
              
            </Menu.Item>
          </Menu>
        </div>
        <div className="right-content" style={{margin: 0,}}>
          {
            selectedKeys[0] === 'basicInfo' && this.renderBasicInfo()
          }
          {
            selectedKeys[0] === 'accountManager' && this.renderAccountManager()
          }
          {
            selectedKeys[0] === 'messageInfo' && this.renderMessageInfo()
          }
        </div>
      </div>
    )
  }
}