import React, { Component } from 'react';
import ColumnItem from '../../components/TaskBoardComponents/ColumnItem/ColumnItem';
import moment from 'moment';
import './index.css';
import { Input } from 'antd';

export default class TaskBoard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      id: localStorage.getItem('knowledgeId'),
      name: '',
      taskModules: [
        {
          title: '',
          id: 'todo',
          children: [],
        },
        {
          title: '',
          id: 'knowledge',
          children: [],
        },
        {
          title: '',
          id: 'market',
          children: [],
        },
        {
          title: '',
          id: 'design',
          children: [],
        },
        {
          title: '',
          id: 'work',
          children: [],
        },
        {
          title: '',
          id: 'check',
          children: [],
        },
        {
          title: '',
          id: 'done',
          children: [],
        },
        {
          title: '',
          id: 'success',
          children: [],
        }
      ]
    }
  }

  componentDidMount() {
    const { taskModules } = this.state;
    const date = moment().subtract(7, 'day');
    const finallyDate = moment().subtract(-14, 'day');
    const types = ['ppt', 'wps', 'pdf', 'sheet'];
    taskModules.map((item, index) => {
      const sum = parseInt(Math.random() * taskModules.length + 4);
      for (let i = 0; i < sum; i++) {
        item.children.push({
          creator: '',
          owner: '',
          createTime: date,
          finallyDate,
          status: (i + 1) % 2,
          tag: '',
          title: '',
          content: ',',
          level: parseInt(Math.random() * 4),
          accessory: ((i + 1) % 2 == 1) && {
            title: `.${types[parseInt(Math.random() * 4)]}`,
            size: '1.25 MB'
          }
        })
      }
    })

    this.setState({
      taskModules,
    })
  }

  render() {
    const { id, name, taskModules } = this.state;
    return (
      <div className="task-board flex">
        <div className="header flex">
          <h3>
            <img className="logo" width={48} src={require('../../assets/images/logo.png')} alt=""/>
            <span>
              <a className="backBtn" href={`#/course/detail/${id}`}>{name}</a> / 
            </span>
          </h3>
          <Input.Search className="search-bar" placeholder="" />
        </div>
        <div className="main-content">
          {
            taskModules.map(item => <ColumnItem {...item} />)
          }
        </div>
      </div>
    )
  }
}