import React, { useState } from 'react';
import { Modal, Form, Input, DatePicker, Button } from 'antd';
import locale from 'antd/es/date-picker/locale/zh_CN';
import Icon from '../../../public/Icon/Icon';

const { RangePicker } = DatePicker;
const statusList = ['', ''];
const statusClassNames = ['doing', 'finish'];
const iconTypes = {
  '': 'iconzu318',
  '': 'iconzu317',
  '': 'iconzu319',
  '': 'iconzu324',
  '': 'iconzu321',
  '': 'iconzu322',
  '': 'iconzu318',
}

export default function ModuleDetailModal(props) {
  const [form] = Form.useForm()

  const renderTitle = (title, status, content) => {
    return (
      <div className="flex">
        <h3>{title}  {status === 1 ? '' : content ? '' : ''}</h3>
        <span className={`${statusClassNames[status]} tags`}>
          {statusList[status]}
        </span>
      </div>
    )
  }

  const renderLabel = (label) => {
    return (
      <div>
        <Icon className="left-icon" type={iconTypes[label]}/>
        <span>{label}</span>
      </div>
    )
  }

  const handleOk = () => {
    form.validateFields().then(values => {
      // form.resetFields();
      onChange(values);
    })
  }

  const { 
    title, 
    status, 
    visible, 
    onCancel, 
    createTime, 
    finallyDate,
    courseTitle,
    parentTitle,
    onChange,
  } = props;

  const formItemLayout = {
    labelCol: {
      xs: { span: 16 },
      sm: { span: 4 },
    },
    wrapperCol: {
      xs: { span: 8 },
      sm: { span: 16 },
    },
  };

  return (
    <Modal 
      width="720px"
      visible={visible}
      onCancel={() => {
        onCancel();
      }}
      closable={false}
      destroyOnClose
      title={renderTitle(parentTitle, status, title)}
      footer={
        [
        status === 0 && <Button key="back" onClick={() => onCancel()}>
          
        </Button>,
        <Button key="submit" type="primary" onClick={() => handleOk()}>
          {status === 0 ? '' : ''}
        </Button>]
      }
    >
      <Form
        form={form}
        initialValues={{
          ...props,
          rangeTime: [createTime, finallyDate]
        }}
        {...formItemLayout}
      >
        <Form.Item 
          name="owner" 
          label={renderLabel('')}
          rules={[{ required: true, message: '' }]}
        >
          <Input disabled={status == 1} />
        </Form.Item>
        <Form.Item 
          name="rangeTime" 
          label={renderLabel('')}
          rules={[{ required: true, message: '' }]}
        >
          <RangePicker locale={locale} disabled={status == 1} />
        </Form.Item>
        <Form.Item 
          name="content" 
          label={renderLabel('')}
          rules={[{ required: true, message: '' }]}
        >
          <Input.TextArea disabled={status == 1} />
        </Form.Item>
        <Form.Item 
          name="tag" 
          label={renderLabel('')}
        >
          <Input disabled={status == 1} />
        </Form.Item>
        <Form.Item label={renderLabel('')}>
          {courseTitle}{parentTitle}
        </Form.Item>
      </Form>
    </Modal>
  )
}
