import React, { Component } from 'react';
import SearchBar from '../../../public/SearchBar';
import SharedTable from '../../../public/SharedTable';
import Icon from '../../../public/Icon/Icon';

import './index.css';

export default class Knowledges extends Component {
  constructor(props) {
    super(props);

    let { columns } = this.props;

    if (!columns) {
      columns = [
        {
          title: '',
          key: 'knowledgeName',
          width: 280,
        },
        {
          title: '',
          key: 'belong',
          width: 240,
          className: 'desc-paragraph',
        },
        {
          title: '',
          key: 'knowledgeDesc',
          width: 360,
          className: 'desc-paragraph',
        }
      ];
    }

    this.state = {
      columns,
      knowledgeList: [],
    };
  }

  componentDidMount() {
    const { knowledgeList } = this.state;
    for (let i = 0; i < 8; i++ ) {
      knowledgeList.push({
        id: `3sjdfash${i}`,
        icon: () => <Icon type="iconzu112" className="middle-icon" />,
        knowledgeName: '-',
        belong: '',
        knowledgeDesc: '',
        behavior: '',
        updatedTime: '14: 23'
      })
    }
    this.setState({
      knowledgeList,
    })
  }
  
  render() {
    const { columns, knowledgeList } = this.state;
    return <div className="knowledge-list">
    <SearchBar 
      onSearch={values => this.onSearch(values)} 
      placeholderText="" 
      searchTitle="" 
      searchBtnText="" 
      onClick={() => {
        console.log(this.props, 'props');
        this.props.history.push('/knowledge/createNew')
      }}
    />
    <SharedTable parentPath="#/knowledge/detail/" columns={columns} dataSource={knowledgeList} />
  </div>
  }
}