import React, { Component } from 'react';
import { Radio, Button, Form, Input, Select, Avatar, Upload } from 'antd';
import Icon from '../../../../public/Icon/Icon';

import './index.css';

const { Option } = Select;

export default class GroupCreateNew extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentStep: 0,
      templateType: 'Auto',
      templateOptionsPerson: [],
      templateOptionsClass: [],
      currentTag: 'class',
      selectedTemplate: undefined,
    };
  }

  componentDidMount() {
    const { templateOptionsPerson, templateOptionsClass, currentTag } = this.state;
    for (let i = 0; i < 2; i++) {
      templateOptionsPerson.push({
        id: `template_${i}`,
        templateName: '',
        templateDesc: '',
      });
    };

    for (let i = 0; i < 4; i++) {
      templateOptionsClass.push({
        id: `template_class_${i}`,
        templateName: '',
        templateDesc: '',
      })
    }

    this.setState({
      templateOptionsPerson,
      templateOptionsClass,
      selectedTemplate: currentTag === 'class' ? templateOptionsClass[0].id : templateOptionsPerson[0].id,
    });
  }

  onStepChange = (step) => {
    this.setState({
      currentStep: step,
    });
  }


  onSelectTag = (e) => {
    const { templateOptionsClass, templateOptionsPerson } = this.state;

    this.setState({
      currentTag: e.target.value,
      selectedTemplate: e.target.value === 'class' ? templateOptionsClass[0].id : templateOptionsPerson[0].id,
    })
  }

  onSelectTemplate = e => {
    this.setState({
      selectedTemplate: e.target.value,
    })
  }

  renderLeftSection = () => {
    const { currentStep, templateOptionsPerson, templateOptionsClass, currentTag, selectedTemplate } = this.state;
    const options = currentTag === 'class' ? templateOptionsClass : templateOptionsPerson;
    
    if (currentStep == 0) {
      return (
        <>
          <div className="tagSection section">
            <h3></h3>
            <Radio.Group onChange={this.onSelectTag} className="tagSelector" value={currentTag} buttonStyle="solid">
              <Radio.Button value="person">
                
              </Radio.Button>
              <Radio.Button value="class">
                
              </Radio.Button>
            </Radio.Group>
          </div>
          <div className="templateSection section">
            <h3>{currentTag === 'class' ? '' : '' }</h3>
            <Radio.Group onChange={this.onSelectTemplate} className="templateSelector" buttonStyle="solid" value={selectedTemplate}>
              {
                options.map(template => (
                  <Radio.Button value={template.id}>
                    <h3>
                      {template.templateName}
                    </h3>
                    <p className="desc-paragraph">
                      {template.templateDesc}
                    </p>
                  </Radio.Button>
                ))
              }
            </Radio.Group>
          </div>
          <Button type="primary" onClick={() => this.onStepChange(1)} className="actionButton">
            
          </Button>
        </>
      )
    } else if (currentStep == 1) {
      return (
        <>
          <div className="goBack">
            <Icon onClick={() => this.onStepChange(0)} type="iconlujing36" />
            <span className="tipText"></span>
          </div>
          <Form
            className="groupForm"
            layout="vertical"
          >
            <Form.Item
              label=""
              name="groupName"
              rules={[{ required: true, message: '' }]}
            >
              <Input placeholder="" />
            </Form.Item>
            <Form.Item
              label=""
              name="groupDesc"
            >
              <Input.TextArea placeholder="" />
            </Form.Item>
            <Form.Item
              label=""
              name="belongs"
            >
              <div className="belongTag">
                
              </div>
            </Form.Item>
            <Form.Item
              label=""
              name="visibleRange"
            >
              <Select className="visibleRangeSelector" defaultValue="team">
                <Option value="team">
                  <h3>
                    <Icon className="marginRight" type="iconlujing40" />
                  </h3>
                  <p className="desc-paragraph">
                    
                  </p>
                </Option>
                <Option value="public">
                  <h3>
                    <Icon className="marginRight" type="iconlujing40" />
                    
                  </h3>
                  <p className="desc-paragraph">
                    
                  </p>
                </Option>
              </Select>
            </Form.Item>
            <Form.Item
              label=""
              name="avatar"
            >
              <div className="flex noJustify">
                <Avatar size={64} />
                <Upload className="uploadBox">
                  <Button className="defaultBtn">
                    
                  </Button>
                  <p className="noMargin desc-paragraph"></p>
                </Upload>
              </div>
            </Form.Item>
            <Form.Item>
              <Button className="primaryBtn" block={true} type="primary"></Button>
            </Form.Item>
          </Form>
        </>
      )
    }
  }

  renderRightSection = () => {
    const { templateType } = this.state;
    
    switch (templateType) {
      case 'Auto':
        return (
          <>
            <div className="templateTitle">
              <p className="noMargin"></p>
              <p className="desc-paragraph">
                
              </p>
            </div>
            <div className="templateSlotPic">
              {/*  */}
              <img src={require('../../../../assets/images/placeHolder.jpg')} />
            </div>
            <div className="useScene">
              <p className="noMargin"></p>
              <p className="desc-paragraph"></p>
            </div>
          </>
        )
        break;
    
      default:
        break;
    }
  }

  render() {
    return (
      <div className="createNewWrap">
        <div className="title flex noJustify">
          <h3></h3>
          <span></span>
        </div>
        <div className="createContent flex">
          <div className="leftSection padding flex">
            {this.renderLeftSection()}
          </div>
          <div className="rightSection padding">
            {this.renderRightSection()}
          </div>
        </div>
      </div>
    )
  }
}